/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 1.5rem;
  background-color: #e0e8ff;
  background-image: url(/images/pattern-background-desktop.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  color: #000000;
  line-height: 1.3;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 620px) {
  body {
    background-image: url(/images/pattern-background-mobile.svg);
  }
}

main {
  display: flex;
  flex-flow: column nowrap;
  overflow: hidden;
  background-color: #ffffff;
  border-radius: 15px;
  max-width: 450px;
  height: 700px;
  min-width: 327px;
  width: 100%;
  transition: all 0.5s cubic-bezier(0.59, 2.34, 0.64, 0.75);
}
@media only screen and (max-width: 620px) {
  main {
    height: 570px;
  }
}
@media only screen and (max-width: 620px) {
  main .hero > img {
    width: 100%;
  }
}
main .info {
  padding: 0rem 4rem;
  text-align: center;
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  justify-content: space-evenly;
}
@media only screen and (max-width: 620px) {
  main .info {
    padding: 0rem 2rem;
  }
}
main .info > p {
  color: #7280a7;
  font-weight: 500;
}
main .info .order {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  border-radius: 15px;
  background-color: #f5f7ff;
  padding: 1rem;
}
main .info .order .OrderInfo {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  margin-left: 1rem;
}
main .info .order .OrderInfo h3 {
  color: #1f2f56;
  margin-bottom: 0.2rem;
  font-size: 1rem;
  font-weight: 900;
}
main .info .order .OrderInfo p {
  color: #7280a7;
}
main .info .order a {
  margin-left: auto;
  font-size: small;
  font-weight: 700;
  transition-duration: 0.3s;
  text-decoration: underline 0.125rem;
}
main .info .order a:hover {
  text-decoration: underline 0.125rem rgba(255, 255, 255, 0);
}
main .info .btnPayment {
  background-color: #3829e0;
  border: none;
  border-radius: 10px;
  padding: 1rem;
  color: #f5f7ff;
  font-weight: 900;
  box-shadow: 0px 15px 15px -5px #3829e066;
  letter-spacing: 1px;
  transition-duration: 0.5s;
}
main .info .btnPayment:hover {
  cursor: pointer;
  opacity: 0.7;
}
main .info > a {
  text-decoration: none;
  color: #7280a7;
  font-size: smaller;
  font-weight: 900;
  align-self: center;
  transition-duration: 300ms;
}
main .info > a:hover {
  color: #1f2f56;
}

html {
  font-family: "Red Hat Display", sans-serif;
}

h1, h2, h3 {
  margin: 0px;
}

h1 {
  font-size: 1.625rem;
  font-weight: 900;
  color: #1f2f56;
}

body {
  font-size: 1rem;
}

p {
  margin: 0%;
}